import { createAPI } from '~/ui/utils/api';

const firstUsageKey = 'user_first_usage';
export const extensionAPI = createAPI({
    name: 'extension',
    default: () => {},
    async read() {},
    methods: {
        async didFirstClick(): Promise<boolean> {
            const didFirstClick = localStorage.getItem(firstUsageKey) ? true : false;
            return didFirstClick;
        },
        async doFirstClick(): Promise<void> {
            localStorage.setItem(firstUsageKey, 'true');
        },
    },
});
